.review {
  &__heading {
    text-align: center;
    margin-bottom: 42px;
    margin-left: auto;
    margin-right: auto;
    max-width: 786px;
    @include media(540) {
      margin-bottom: 11px;
    }
    .heading__title {
      margin-bottom: 29px;
    }
  }
  $gap: 10px;
  &__row {
    margin: -$gap;
  }
  &__item {
    margin: $gap;
  }

  &__button {
    margin-top: 74px;
    left: 50%;
    position: relative;
    transform: translateX(-50%);
    @include media(1200) {
      margin-top: 58px;
    }
    @include media(540) {
      margin-top: 42px;
    }
  }
  &__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    padding: 10px 20px;
    color: $primary;
    @include hover {
      color: $dark;
    }
    @include media(540) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
  &__arrow-left {
    left: -30px;
    @include media(540) {
      left: -10px;
    }
  }
  &__arrow-right {
    right: -30px;
    @include media(540) {
      right: -10px;
    }
  }
}